.page{
	&-admin{
		.form-group{
			label{
				margin-top:8px !important;
			}
			label > input[type="radio"]{
				margin-left: 5px;
			}
		}

		.btn-group{
			.glyphicon{
				top: 3px;
			}
			.btn{
				&-toggable{
					&.active{
						color: white;
						background: rgba(125,200,0,1);
					}
				}
				&-priority{
					cursor: default;
					&:hover{
						background-color: rgba(255,255,255,1);
					}
					width: 75px;
				}
			}
		}
		.filters{
			margin-top: 10px;
			margin-bottom: 10px;
		}

		.projects-list{
			list-style: none;
			padding:0;
			margin:0;
			li{
				margin-top: 2px;
			}
		}
	}
}
